<template>
  <el-dialog title="裁剪图片" :visible.sync="dialogVisible" :close-on-click-modal="false" width="60%" @opened="dialogOpened" :before-close="closeDialog">
    <el-row>
      <el-col :span="18" style="height: 500px">
        <vue-picture-cut ref="pictureCut" :menu-thickness="0" :src="src"></vue-picture-cut>
      </el-col>
      <el-col :span="5" :offset="1">
        <div class="btn-box">
          <label>旋转图片：</label>
          <div style="display: inline-block; white-space: nowrap">
            <el-button type="text" @click="imgLeft">
              <i class="el-icon-refresh-left"></i>
            </el-button>
            <el-button type="text" @click="imgRight">
              <i class="el-icon-refresh-right"></i>
            </el-button>
          </div>
        </div>
        <div class="btn-box">
          <label>旋转度数：</label>
          <el-input-number
            v-model="cut.angle"
            :controls="false"
            size="mini"
            :precision="0"
            :min="-180"
            :max="180"
            @change="angleBlur"
          ></el-input-number>
        </div>
        <div class="btn-box">
          <label>翻转镜像：</label>
          <div style="display: inline-block; white-space: nowrap">
            <el-button type="text" @click="reverLevel"><i class="el-icon-sort vertial"></i></el-button>
            <el-button type="text" @click="reverVertical"><i class="el-icon-sort"></i></el-button>
          </div>
        </div>
        <div class="btn-box">
          <label>裁剪形状</label>
          <el-radio-group v-model="cut.shape">
            <el-radio label="rect">矩形</el-radio>
            <el-radio label="circle">椭圆</el-radio>
          </el-radio-group>
        </div>
        <div class="btn-box">
          <label>锁定比例</label>
          <el-radio-group v-model="cut.isRatio">
            <el-radio :label="false">否</el-radio>
            <el-radio :label="true">是</el-radio>
          </el-radio-group>
        </div>
        <div v-if="cut.isRatio" class="btn-box">
          <label>裁剪比例：</label>
          <el-input v-model="cut.ratio" size="mini" @change="changeRatio"></el-input>
          <p v-if="cut.badRatio" class="info">裁剪比例填写格式应为 宽:高</p>
        </div>
      </el-col>
    </el-row>
    <template v-slot:footer>
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="cutImg">确 定</el-button>
    </template>
  </el-dialog>
</template>

<script>
  import { VuePictureCut, createUtils } from "vue-picture-cut";
  export default {
    name: "pictureCutDialog",
    props:{
      dialogVisible: {
        type: Boolean,
        default: false
      },
      src: {
        default: ""
      }
    },
    components:{
      VuePictureCut
    },
    data(){
      return{
        cut: {
          shape: "rect",
          isRatio: false,
          badRatio: false, //判断比例的格式是否填写正确
          angle: 0,
          angleStep: 45, //每次旋转多少度
          ratio: "",
        },
        utils: null, //调用裁剪的工具
  
      }
    },
    watch:{
      "cut.isRatio"(val) {
        this.utils.setMaskResize(!val);
      },
      "cut.shape"(val) {
        this.utils.setMaskRound(val === "circle");
      },
    },
    methods:{
      imgLeft() {
        const temp = this.cut.angle;
        const current = temp - this.cut.angleStep;
        this.cut.angle = current < -180 ? 360 + current : current;
        this.utils.rotate(this.cut.angleStep, true);
      },
      imgRight() {
        const temp = this.cut.angle;
        const current = temp + this.cut.angleStep;
        this.cut.angle = current > 180 ? current - 360 : current;
        this.utils.rotate(-this.cut.angleStep, true);
      },
      changeRatio(val) {
        try {
          let width = val.split(":")[0];
          let height = val.split(":")[1];
          if (!width || !height) {
            this.cut.badRatio = true;
            return;
          }
          if (!_.isNumber(+width) || !_.isNumber(+height)) {
            this.cut.badRatio = true;
            return;
          }
          this.utils.setMaskSize(width, height);
          this.cut.badRatio = false;
        } catch (e) {
          this.cut.badRatio = true;
        }
      },
      reverLevel() {
        this.utils.setFlipH();
      },
      reverVertical() {
        this.utils.setFlipV();
      },
      angleBlur() {
        this.utils.rotateTo(-this.cut.angle, true);
      },
      cutImg() {
        const self = this;
        const cutData = self.utils.cut(undefined, 1, "image/png");
        //cutData.file 是一个Blob 对象, 需要将其转为File 对象
        const file = new File([cutData.file], '封面.png', {type: "image/png"})
        self.$emit("cut-img", file)
      },
      dialogOpened() {
        this.utils = createUtils(this.$refs["pictureCut"]);
      },
      closeDialog(){
        this.$emit("dialog-close")
      }
    }
  }
</script>